import { Component, OnInit } from '@angular/core';
import {UserServiceService} from '../user-service.service'
import { Subscription } from 'rxjs';
import { DataService } from 'src/app/home/data.service';

@Component({
  selector: 'app-update-password',
  templateUrl: './update-password.component.html',
  styleUrls: ['./update-password.component.css']
})
export class UpdatePasswordComponent implements OnInit {

  email;

  constructor(private _userService : UserServiceService, private dataService: DataService) {

    this.subscription = this.dataService.getMessage().subscribe(message => {
      if (message) {
      this.messages.push(message);
      console.log(message);
      console.log("Message",message.text.email);
      this.email = message.text.email;
      } else {
      // clear messages when empty message received
      this.messages = [];
      }
      });
   }


  messages: any[] = [];
    subscription: Subscription;
  ngOnInit() {
    
  }

  update(email: string) {
    this._userService.updatePassword(email)
      .subscribe(
        data => {
          console.log(email);
        },
        error => console.error(error)
      );

  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}
